.dialogs {
  display: grid;
  grid-template-columns: 2fr 10fr;
  text-decoration: none; }
.dialogs {
  &-items {
    display: flex;
    flex-direction: column; } }
.dialogs-items,
.messages {
  padding: 10px; }
.dialog {
  text-decoration: none;
  margin-block-start: 1.33em; }

.messages {}

.message {
  margin-block-start: 1.33em; }
